/**
 * axios
 */
import request from '@/utils/request'

/**
 * 
 * hitokoto()en()social()soup()fart()zha()
 */
export const oneSay = () => {
  return request({
    method: 'GET',
    url: 'https://api.uixsj.cn/hitokoto/get?type=fart&code=json'
  })
}
/**
 * 
 * /name  /all 
 */
export const nameOrAll = () => {
  return request({
    method: 'GET',
    url: 'http://127.0.0.1:8080/'
  })
}
